.nav-wrapper {
  display: flex;
  justify-content: flex-end;
  font-size: 28px;
  align-items: flex-end;
}

.nav-wrapper span {
  margin-left: 5%;
}
a {
  text-decoration: none;
  color: rgb(63, 61, 61);
  font-weight: 600;
}
div span img {
    height: 50px;
}

@media (max-width: 900px) {
  .nav-wrapper {
    font-size: 18px;
  }
}

@media (max-width: 500px) {
  .nav-wrapper {
    font-size: 12px;
  }

  div span img {
    height: 25px;
    width: 25px;
}

  .nav-wrapper span {
    margin-left: 2%;
  }
}
